<template>
  <div style="min-width: 700px">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>主密钥管理</el-breadcrumb-item>
      <el-breadcrumb-item>密钥材料</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="获取密钥参数" name="first">
          <material-get></material-get>
        </el-tab-pane>
        <el-tab-pane label="导入密钥材料" name="second">
          <material-import></material-import>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>
<script>
  import MaterialGet from './Material-get.vue'
  import MaterialImport from './Material-import.vue'
  export default {
    components: {
      MaterialGet,
      MaterialImport
    },
    data() {
      return {
        activeName: 'first'
      };
    },
    beforeRouteEnter: (to, form, next) => {
      const loginType = window.sessionStorage.getItem('accountTypeLogin');
      if (loginType === '9') {
        next()
      }else{
        return next('/404')
      }
    },
    methods: {
      handleClick(tab, event) {
//        console.log(tab, event);
//        this.$refs.form[tab.name].resetFields();
      }
    }
  };
</script>
<style lang="less" scoped>
