<template>
  <div>
    <el-form :model="importForm" :rules="importFormRules" ref="importFormRef" width="600px" status-icon
             label-width="150px">
      <el-form-item label="密钥标识" prop="importKeyId">
        <el-input v-model.trim="importForm.importKeyId"></el-input>
      </el-form-item>
      <el-form-item label="导入令牌" prop="importToken">
        <el-input type="textarea" :rows="10" v-model.trim="importForm.importToken"></el-input>
      </el-form-item>
      <el-form-item label="密钥材料" prop="encryptedKeyMaterial">
        <el-input type="textarea" :rows="10" v-model.trim="importForm.encryptedKeyMaterial"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitget">提交</el-button>
        <el-button @click="resetForm('importFormRef')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        importForm: {
          importKeyId: '',
          importToken: '',
          publicKey: '',
        },
        importFormRules: {
          importKeyId: [{required: true, message: '请输入密钥标识', trigger: 'blur'}],
          importToken: [{required: true, message: '请输入导入令牌', trigger: 'blur'}],
          encryptedKeyMaterial: [{required: true, message: '请输入密钥材料', trigger: 'blur'}],
        },
      }
    },
    methods: {
      submitget() {
        this.$refs.importFormRef.validate(async valid => {
          if (!valid) return;
          const {data: res} = await this.$http.post('v1/kms/ImportKeyMaterial', {
            'keyId': this.importForm.importKeyId,
            'importToken': this.importForm.importToken,
            'encryptedKeyMaterial': this.importForm.encryptedKeyMaterial,
            'keyMaterialExpireUnix': 0
          });
          if (res.code !== 0) return this.$message.error((res.msg !== '' && res.msg !== null) ? res.msg : '导入主密钥（CMK）材料失败！');
          this.$message.success('导入主密钥（CMK）材料成功！');
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }

</script>
<style scoped lang="less">
  .el-form {
    width: 600px;
    padding: 20px;
  }

  .btn_div {
    padding: 20px;
  }
</style>
