<template>
  <div>
    <el-form :model="importForm" :rules="importFormRules" ref="importFormRef" status-icon label-width="150px">
      <el-form-item label="密钥标识" prop="importKeyId">
        <el-input v-model.trim="importForm.importKeyId"></el-input>
      </el-form-item>
      <el-form-item label="公钥类型/加密算法" prop="selectedOptions">
        <el-cascader style="width: 100%" :options="options" clearable
                     v-model.trim="importForm.selectedOptions"></el-cascader>
      </el-form-item>
      <el-form-item label="结果" prop="result">
        <el-input type="textarea" :readonly="true" :rows="20" v-model.trim="importForm.result"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitget">提交</el-button>
        <el-button @click="resetForm('importFormRef')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      const keyBlur = (rule, val, cb) => {
        if (val === '') {
          cb(new Error('请输入密钥标识'));
        } else {
          this.getExternal(val,cb)
        }
      };
      return {
        options: [
          // {
          //   value: 'RSA_2048',
          //   label: 'RSA_2048',
          //   children: [{value: 'RSAES_PKCS1_V1_5', label: 'RSAES_PKCS1_V1_5'}, {
          //     value: 'RSAES_OAEP_SHA_1',
          //     label: 'RSAES_OAEP_SHA_1'
          //   }, {value: 'RSAES_OAEP_SHA_256', label: 'RSAES_OAEP_SHA_256'}]
          // },
          {value: 'EC_SM2', label: 'EC_SM2', children: [{value: 'SM2PKE', label: 'SM2PKE'}]}
        ],
        importForm: {
          importKeyId: '',
          selectedOptions: '',
          result: ''
        },
        importFormRules: {
          // importKeyId: [{required: true, message: '请输入密钥标识', trigger: 'blur'}],
          importKeyId: [{required: true, validator: keyBlur, trigger: 'blur'}],
          selectedOptions: [{required: true, message: '请选项公钥类型/加密算法', trigger: 'change'}],
        },
      }
    },
    methods: {
      submitget() {
        this.$refs.importFormRef.validate(async valid => {
          if (!valid) return;
          const {data: res} = await this.$http.post('v1/kms/GetParametersForImport', {
            'keyId': this.importForm.importKeyId,
            'wrappingAlgorithm': this.importForm.selectedOptions[1],//SM2PKE
            'wrappingKeySpec': this.importForm.selectedOptions[0]//EC_SM2
          });
          if (res.code !== 0) return this.$message.error((res.msg !== '' && res.msg !== null) ? res.msg : '获取导入主密钥（CMK）材料的参数失败！');
          this.importForm.result = "导入令牌：" + res.data.importToken + " \n" + " \n" + "公钥：" + res.data.publicKey
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      async getExternal(val,cb) {
        const {data: res} = await this.$http.get('v1/kms/isExternal',{params:{
          'keyId':val
        }});
        if (res.code !== 0) return cb(res.msg);
        return cb()
      }
    }
  }

</script>
<style scoped lang="less">
  .el-form {
    width: 600px;
  }

  .btn_div {
    padding: 20px;
  }
</style>
